import React from 'react'
import style from './Header.module.css'
import Menu from './Menu/Menu';
import logo from '../../img/logo.png'

class Header extends React.Component {
    render() {
        return (<header>
                <div className={style.learnLinks + " row"}>
                    <div className="logo col-md-2">
                        <img src={logo} alt="logo"/>
                    </div>
                    <div className={style.linkContainer + " col-md-1"}>
                        <a href="#">Learn about VELOCITY</a>
                    </div>
                    <div className={style.linkContainer + " col-md-1"}>
                        <a href="#">Learn about CROSSES</a>
                    </div>
                    <div className={style.linkContainer + " col-md-1"}>
                        <a href="#">Learn about TRIGGERS</a>
                    </div>
                    <div className={style.headerContainer + " col-md-3 offset-4"}>
                        <h1>Global professional</h1>
                    </div>
                </div>
                <Menu/>
                <div className={style.categories + " row"}>
                    <div className="col-md-4 offset-1 row">
                        <div className="col-md-4">
                            <p>Name</p>
                        </div>
                        <div className="col-md-2">
                            <p>Last</p>
                        </div>
                        <div className="col-md-2">
                            <p>Chg.%</p>
                        </div>
                        <div className="col-md-4">
                            <p>Velocity</p>
                        </div>
                    </div>
                    <div className="col-md-7 row">
                        <div className="col-md-5 row">
                            <div className="col-md-3">
                                <p>M Trigger</p>
                            </div>
                            <div className="col-md-3">
                                <p>W Trigger</p>
                            </div>
                            <div className="col-md-3">
                                <p>D Trigger</p>
                            </div>
                            <div className="col-md-3">
                                <p>H Trigger</p>
                            </div>
                        </div>
                        <div className="col-md-5 offset-1 row">
                            <div className="col-md-3">
                                <p>M Velocity</p>
                            </div>
                            <div className="col-md-3">
                                <p>W Velocity</p>
                            </div>
                            <div className="col-md-3">
                                <p>D Velocity</p>
                            </div>
                            <div className="col-md-3">
                                <p>H Velocity</p>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
        )
    }
}

export default Header;