import axios from 'axios';

axios.defaults.baseURL = 'https://cloud.iexapis.com/stable';
axios.defaults.headers.post['Content-Type'] = 'application/json';

const token = 'token=sk_0cc1e265ec3d43618838595f9549ba45';

const stocksSymbols = ['FB', 'AAPL', 'AMZN', 'NFLX', 'GOOG', 'NVDA', 'INTC', 'CGC', 'TSLA', 'CAT', 'VIXY', 'DIA', 'SPY',
    'IYT', 'QQQ', 'IWM', 'IBB', 'HYG', 'TLT', 'UUP'];

export let getSymbols = async () => {
    let result = [];
    for(let i = 0; i < stocksSymbols.length; i++){
        await axios.get(`/stock/${stocksSymbols[i]}/quote?${token}`)
            .then(item => result.push(item.data))
            .catch(error => console.log(error));
    }
    return result;
};