import React from 'react';
import style from './SymbolList.module.css';
import { getSymbols } from "../../../api/iex";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay } from '@fortawesome/free-solid-svg-icons';

const icon = <FontAwesomeIcon icon={faPlay}/>;

class SymbolList extends React.Component {
    state = {
        symbols: [],
        isLoading: true
    };

    async componentDidMount() {
        let symbols = await getSymbols();
        this.setState(() => ({
            symbols,
            isLoading:false
        }));
        console.log(this.state.symbols);
    }

    render() {
        if(!this.state.isLoading)
        return (
            <div className={"col-md-4 offset-1"}>
                <ul className={style.symbolsContainer}>
                    {
                        // console.log(this.state.symbols)
                        this.state.symbols.map(item => <li className={style.symbolStyle + ' row'}>
                            <span className={style.playIcon + ' col-md-4'}><span>{icon}</span>{item.companyName.substr(0, 15)}{item.companyName.length >= 15 ? '...' : ''}</span>
                            <span className='col-md-2'>{item.latestPrice}</span>
                            <span className={item.changePercent>0?style.textGreen+' col-md-2':style.textRed + ' col-md-2'}>{item.changePercent}</span>
                            <span className='col-md-4'>Velocity</span>
                        </li>)
                    }
                </ul>
            </div>);
        else
            return(<div className={"col-md-4 offset-1"}>
                <p>loading...</p>
            </div>)
    }
}

export default SymbolList;